"use client";
import React, { useEffect, useState } from "react";
import { RootState, useAppDispatch, useAppSelector } from "../Redux";
import LoginEventsModal from "../components/LoginEventModal";
import { userDetailAsync } from "../Redux/userSlice";
import { fetchuserLoginEvents } from "../Redux/userLoginEventsSlice";
import { Info } from "lucide-react"; // Importing Info icon

const Dashboard: React.FC = () => {
  const dispatch = useAppDispatch();
  const [isModalOpen, setIsModalOpen] = useState(false);
  const [selectedUserId, setSelectedUserId] = useState<string | null>(null);
  const [infoModalData, setInfoModalData] = useState(null);
  const userDetails: any = useAppSelector((state: RootState) => state.user);

  useEffect(() => {
    dispatch(userDetailAsync());
  }, [dispatch]);

  const handleRowClick = async (userId: any) => {
    setSelectedUserId(userId);
    setIsModalOpen(true);
    dispatch(fetchuserLoginEvents(userId));
  };

  const handleCloseModal = () => {
    setIsModalOpen(false);
    setSelectedUserId(null);
  };

  const handleInfoClick = (user: any) => {
    setInfoModalData(user);
  };

  const handleCloseInfoModal = () => {
    setInfoModalData(null);
  };

  // Sorting users based on riskFactor in ascending order
  const sortedUsers = Array.isArray(userDetails?.userDetails?.userlisting)
    ? [...userDetails.userDetails.userlisting].sort(
        (a, b) => a.riskFactor - b.riskFactor
      )
    : [];

  return (
    <div className="bg-white shadow-lg rounded-lg p-6">
      <div className="flex justify-start items-start mb-4">
        <h1 className="assign-text ps-2 py-2">Assign Carrier</h1>
      </div>
      <table className="w-full text-sm text-left text-gray-500 border rounded-lg">
        <thead>
          <tr>
            <th className="px-6 py-3 bg-blue-500 text-white font-bold">Name</th>
            <th className="px-6 py-3 bg-blue-500 text-white font-bold">Email ID</th>
            <th className="px-6 py-3 bg-blue-500 text-white font-bold">Contact Number</th>
            <th className="px-6 py-3 bg-blue-500 text-white font-bold">Country</th>
            <th className="px-6 py-3 bg-blue-500 text-white font-bold">State</th>
            <th className="px-6 py-3 bg-blue-700 text-white font-bold text-center">Risk Score</th>
            <th className="px-6 py-3 bg-blue-500 text-white font-bold text-center">Action</th>
          </tr>
        </thead>
        <tbody>
          {sortedUsers.map((user, index) => (
            <tr key={index} className="bg-white border-b hover:bg-gray-50">
              <td className="px-6 py-2 flex items-center gap-2">
                {user.name}
                <Info className="w-5 text-blue-500 cursor-pointer" onClick={() => handleInfoClick(user)} />
              </td>
              <td className="px-6 py-2">{user.email}</td>
              <td className="px-6 py-2">{user.contactNumber}</td>
              <td className="px-6 py-2">{user.country}</td>
              <td className="px-6 py-2">{user.state}</td>
              <td className="px-6 py-2 bg-blue-700 text-center">
                <span
                  onClick={() => handleRowClick(user._id)}
                  className={`inline-block border-2 p-2 rounded-lg text-lg font-bold w-24 py-2 text-center ${
                    user.riskFactor < 50 ? "border-green-500 text-green-500" :
                    user.riskFactor < 75 ? "border-yellow-500 text-yellow-500" :
                    "border-red-500 text-red-500"
                  } bg-white`}
                  style={{ borderWidth: "3px" }}
                >
                  {Math.round(user.riskFactor * 100) / 100}%
                </span>
              </td>
              <td className="px-6 py-2 text-center">
                <button className="border-2 border-blue-500 bg-blue-100 text-black font-bold py-2 px-6 rounded-lg shadow-md hover:bg-blue-200 transition">
                  Assign
                </button>
              </td>
            </tr>
          ))}
        </tbody>
      </table>
    </div>
  );
};

export default Dashboard;
